@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@500;700&display=swap);
*{margin:0;padding:0}

*{margin:0;padding:0;font-family:'Manrope';font-size:13px}.container{background-color:rgba(158,175,194,0.4);width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.card{background-color:#ecf2f8;width:80%;display:flex;border-radius:10px;box-shadow:-1px -1px 15px -1px rgba(158,175,194,0.4);z-index:100}.content{display:flex;flex-direction:column;justify-content:center;padding:30px}.image{width:40%;border-top-left-radius:10px;border-bottom-left-radius:10px}.title{font-weight:bold;font-size:1.9em;color:#48556a}.name{font-weight:bold;color:#48556a}.text,.date{margin-top:15px;color:#6d7f97}.item__image>img.image{width:50px;height:50px;border-radius:50%}.item{display:flex;margin-top:15px;width:100%}.item__content{width:100%;display:flex;justify-content:space-between;align-items:center;margin-left:10px;margin-right:10px}button{width:45px;height:45px;border-radius:50%;border:none;background-color:rgba(158,175,194,0.4);cursor:pointer}button.active{opacity:0.85;background-color:#48556a}button:hover{box-shadow:0 0 15px 0 rgba(158,175,194,0.4)}.share.active>path{stroke:#ecf2f8;fill:#ecf2f8}.popover.desktop__active{display:flex;transition:opacity 2s}.popover{display:none;position:absolute;align-items:center;background-color:#48556a;border-radius:10px;padding:5px;right:8.5%;margin-bottom:120px;transition:all 0.5s cubic-bezier(0.75, -0.02, 0.2, 0.97)}.popover::after{position:absolute;content:' ';width:0;height:0;border-style:solid;border-width:15px 20px 0 20px;border-color:#48556a transparent transparent transparent;left:40%;bottom:-30%}.popover>span{cursor:pointer;width:25px;height:25px;text-transform:uppercase;margin-right:10px}.popover>span.text{width:auto;margin:10px 10px 0 10px;color:#ecf2f8;letter-spacing:5px}.facebook{content:url(/article-preview-component-master/static/media/icon-facebook.1e9b3338.svg)}.twitter{content:url(/article-preview-component-master/static/media/icon-twitter.45735e3b.svg)}.pinterest{content:url(/article-preview-component-master/static/media/icon-pinterest.45264b1d.svg)}@media (min-width: 320px) and (max-width: 992px){.container{width:auto;padding:auto}.card{flex-direction:column}.image{width:100%;border-top-right-radius:10px;border-bottom-left-radius:0px}.popover::after{display:none}.popover.desktop__active{display:none}.popover.active{display:flex}.popover{width:100%;height:100px;margin:0;padding:0;right:0;position:static;border-top-left-radius:0;border-top-right-radius:0}button.active{background-color:#6d7f97;opacity:1;z-index:100;position:absolute;margin-top:155px}.texts.active{display:none}.image.active{display:none}.item__content.active{width:100%;margin-top:0;margin-left:-0;margin-bottom:-0;justify-content:flex-end}}

